<template>
  <div class="flex-box" style="height: 100%">
    <el-card class="box-card crd flex-box" style="height: auto">
      <el-form>
        <el-form-item label="当前邮箱">
          <el-input
            v-model="emailInfo.email"
            placeholder="绑定新邮箱或更换邮箱"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <div class="flex-box">
            邮箱验证码：<el-input
              style="width: 70%"
              placeholder="邮箱验证码"
              :disabled="emailDisabled"
              v-model="emailInfo.code"
            ></el-input>
          </div>
        </el-form-item>
        <el-form-item class="flex-box">
          <el-button type="success" @click="sendEmail">发送验证码</el-button>
        </el-form-item>
        <el-form-item label-width="0px">
          <div class="flex-box" style="justify-content: space-evenly">
            <el-button type="primary" @click="updateEmail">确定修改</el-button>
          </div>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import tools from '@/js/tools';
export default {
  data() {
    return {
      emailInfo: {
        code: '',
        email: ''
      },
      emailPopover: false,
      emailDisabled: true
    };
  },
  methods: {
    sendEmail() {
      tools.NoAjax(
        '/tool/sendEmailCode',
        {
          email: this.emailInfo.email
        },
        (data) => {
          if (data.success) {
            this.emailDisabled = false;
            this.emailPopover = true;
            this.$message(data.message);
          } else {
            if (data.message == '验证码长期有效，无需重复发送') {
              this.emailDisabled = false;
              this.emailPopover = true;
            }
            this.$message(data.message);
          }
        },
        true
      );
    },
    updateEmail() {
      tools.NoAjax('/user/auth/updateUserEmail', this.emailInfo, (data) => {
        this.$message(data.message);
        this.$store.dispatch('updatedUser');
        this.emailInfo = {
          code: '',
          email: ''
        };
      });
    }
  },
  created() {
    this.emailInfo.email = this.$store.state.user.tbUserInfo.email;
  }
};
</script>

<style scoped>
.crd {
  padding: 2rem;
}
.el-form-item {
  margin-bottom: 2.5rem;
}
</style>
